input{
    width: 200px;
    height: 20px;
    border: 2px solid gray;
    border-top: 0;
    border-right: 0;
    border-left: 0;
}

textarea:focus, input:focus{
    outline: none;
    border-bottom: 1px solid gray;
    border-top: 0;
    border-right: 0;
    border-left: 0;
}

ul{
   margin: auto;
   width: 55%;
   padding: 10px;
    list-style: none;
    border: 2px solid gray;
    border-top: 0;
}
li{
    border: 1%;
}
li:hover, li:focus{
    background-color: gray;
}